<template>
   <v-row justify="center">
    <v-dialog v-model="dialog"  persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline"> <v-icon color="primary">mdi-calendar-multiple-check</v-icon> Etkinlik Ekle</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
                <v-col cols="12" sm="12">
      <v-text-field
          :error-messages="errTitle"
              v-model="events.title"
              :label="$t('events.title')"
              filled
              counter="50"
              hint="Etkinlik Başlığını Giriniz"
              background-color="transparent"
            ></v-text-field>
    </v-col>
    <v-col cols="12" sm="12">
 <v-textarea
 counter="140"
          :error-messages="errContent"
 v-model="events.content"
          name="input-7-1 w-100"
          :label="$t('events.content')"
          hint="Etkinlik Açıklamasını giriniz"
        ></v-textarea>
    </v-col>
      <v-col cols="12" sm="12">
        <p>Etkinlik Tarihini Seçiniz</p>
        <p class="text-danger">{{errAtDate[0]}}</p>
      <v-date-picker v-model="events.atDate" ></v-date-picker>
    </v-col>
    <v-col cols="12">
        <el-upload
  class="upload-demo"
  drag
  :action="'#'"
  :on-change="fileUpload"
  :multiple="false"
  :limit="1"
  :auto-upload="false">
  <i class="el-icon-upload"></i>
  <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
  <div class="el-upload__tip" slot="tip">jpg/png/pdf dosyalarınu yükleyebilirsiniz ve yüklenilecek dosya 2MB'dan küçük olmalıdır.</div>
</el-upload>
<p class="text-danger">{{errMsg}}</p>
    </v-col>
            </v-row>
          </v-container>
        </v-card-text>
         <v-card-actions>
          <v-spacer></v-spacer>
           <v-btn  class="ma-2" @click="closeModal()" color="info" dark>{{$t('generate.close')}} </v-btn>
           <v-btn :loading="loading" :disabled="loading" class="ma-2" color="primary" @click="sendEvent()" dark>{{$t('generate.submit')}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <snack-bar :snackbar="snackbar" @finished="snackbar=$event"></snack-bar>

  </v-row>
</template>

<script>
export default {
props:{
      dialog:{
        default:false,
        type : Boolean,
        required:true
    },
},
computed:{
dateRangeText () {
        return this.date.join(' ~ ')
      },
},
methods:{
  async sendEvent(){
   this.loading = true
       this.errTitle=[]
      this.errContent=[]
      this.errAtDate=[]
    if(this.errMsg!=''||this.events.title==''||this.events.atDate==''||this.events.content==''|| this.events.file.uid===undefined){
      if(this.events.title==''){
        this.errTitle.push('Etkinlik Başlığı girmeniz gerekmektedir.')
      }
      if(this.events.content==''){
        this.errContent.push('Etkinlik İçeriği girmeniz gerekmektedir.')
      }
      if(this.events.atDate==''){
        this.errAtDate.push('Etkinlik Tarihi girmeniz gerekmektedir.')
      }
       if(this.events.file.length==0){
        this.errMsg = "Yüklenecek dosyayı göndermeniz gerekmektedir."
      }
         this.loading = false
  //  this.errMsg==''
    }
    else{
      let fd = new FormData()
    fd.append('title',this.events.title)
    fd.append('atDate',this.events.atDate)
    fd.append('content',this.events.content)
    fd.append('image',this.events.file)
    let result = await this.$api.postfd('events','file',fd)
    if(result.status=='success'){
      this.$api.tnotify(this.events.atDate+' tarihinde '+ this.events.title + ' etkinliğiniz var!',this.events.content,'teachers')
      this.$api.tnotify(this.events.atDate+' tarihinde '+ this.events.title + ' etkinliğiniz var!',this.events.content,'parents')
      let type = 'jpg'
      if(this.events.file.type.includes('pdf'))
        type='pdf'
      let path = result.path.replace('public/',this.$api.BASE_API_URL)
      this.$emit('setNewFoodList',{atDate: this.events.atDate, content: this.events.content, title : this.events.title, id:result.id, file:path ,type:type})
         this.loading = false
         this.snackbar=true
      this.closeModal()
    }
    else{
         this.loading = false
      alert('hata')
    }
    }
  },
   fileUpload(e){
      if(e.raw.type==' application/pdf ' || e.raw.type=='image/jpeg' || e.raw.type=='image/png'){
          if(e.size>5000000){
       this.errMsg = "Dosya Boyutunuz 5MB üzerinde lütfen " +e.raw.type +' dosyanızı kaldırın. Dosya Boyutu : ' +e.raw.size+' bayt'
      }
      else{
        this.errMsg=""
       this.events.file = e.raw;
      }
      }
      else{
          this.errMsg="Dosya formatınız .pdf, .jpg, .jpeg veya .png olmalıdır."
      }
    },
    closeModal(){
        this.dialog=false
        this.$emit('dialog',false)
        this.events={
            title:"",
            content:"",
            atDate:"",
            file:[]
        }
    }
},
data(){
    return{
        date:[],
        loading:false,
        snackbar:false,
        events:{
            title:"",
            content:"",
            atDate:"",
            file:[]
        },
        errTitle:[],
           errContent:[],
           errAtDate:[],
           errMsg:""
    }
}
}
</script>

<style>
@media only screen and (max-width: 900px) {
  .v-dialog {
    margin : 0px !important
  }
}
.el-upload, .el-upload-dragger {
    width: 100% !important;
}
</style>